main {
  margin-left: 20%;
}
main .image-carousel {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 900px;
  margin-left: 100px;
}
main .image-carousel .carousel-pop {
  position: relative;
  z-index: 2;
}
main .pizza-list {
  display: grid;
  grid-column: 2;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  margin-top: 100px;
}
main .pizza-list img {
  width: 300px;
}

