.pizza-card {
  border: 2px solid gray;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  position: relative;
}

.description {
  color: rgb(143, 143, 143);
}

.add {
  display: inline-block;
  padding: 12px 24px;
  border: none;
  font-size: 16px;
  background-color: inherit;
  border-radius: 100px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.25);
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0);
  cursor: pointer;
  overflow: hidden;
  transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1);
}

.add span:last-child {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background-color: #deba00;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

.add span:first-child {
  position: relative;
  z-index: 1;
}

.add:hover {
  box-shadow: 0 0 0 5px #ffea00;
  color: #000000;
}

.add:active {
  scale: 0.95;
}

.add:hover span:last-child {
  width: 150px;
  height: 150px;
  opacity: 1;
}

.special-offer:before {
  content: "Special Offer";
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: red;
  color: white;
  padding: 5px 10px;
  font-weight: bold;
  border-radius: 5px;
}

