body {
  padding: 0;
}

.main {
  margin-left: 20%;
}
.main .active {
  color: #cccccc;
}
.main .div-price {
  display: flex;
  align-content: center;
  justify-content: center;
}
.main .div-price .price {
  margin: 50px;
  font-size: 33px;
  text-decoration: none;
  position: relative;
}
.main .div-price .price::before, .main .div-price .price::after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.main .div-price .price.ch::before {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 8px solid red;
  bottom: -10px;
  width: 100px;
}
.main .div-price .price.ex::after {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 8px solid green;
  top: -10px;
  width: 100px;
}
.main .pizza-list {
  display: grid;
  grid-column: 2;
  grid-template-columns: 1fr 1fr;
  gap: 100px;
  padding: 16px;
}
.main .loader-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

